.header{
    width: 100%;
    background: #032541;
}
.header .inner{
    width: 1300px;
    height: 65px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    /* border: 1px solid red; */
    align-items: center;
    color: #fff;
    font-weight: bold;
}
.header img{
    width: 140px;
}
.header .logo_wrap{
    display: flex;
    gap: 26px;
}
.header .gnb{
    display: flex;
    gap: 26px;
}
.header .gnb li{
    cursor: pointer;
}
.header .gnb li:hover{
    color: #11adc3;
}
.header .login_wrap2{
    display: flex;
    gap: 26px ;
    cursor: pointer;
}
.header .inner .login_wrap2 div{
    color: #fff;
}
.header .login_wrap2 div:hover{
    color: #11adc3;
}
.login_wrap2 .ko{
    width: 24px;
    height: 22px;
    font-size: 12px;
    border: 1px solid #fff;
    border-radius: 4px;
    text-align: center;
    line-height: 22px;
}

.login_wrap2 .glass{
    font-size: 20px;
    color: #11adc3;
}


