.modal_wrap{
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.8);
    z-index: 100;
}
.modal{
    position: absolute;
    max-width: 500px;
    background: rgba(0, 0, 0, 0.6);
    position: relative;
    box-sizing: border-box;
    border-radius: 10px;
}
.modal_img{
    width: 100%;
    margin-bottom: 10px;
    height: auto;
}
.modal_img img{
    width: 100%;
    box-sizing: border-box;
}
.modal_img span{
    width: 30px;
    height: 30px;
    position: absolute;
    right: 0;
    top: -32px;
    color: #fff;
    border: 1px solid #444;
    background: rgba(0, 0, 0, 0.6);
    cursor: pointer;
    text-align: center;
    line-height: 30px;
}
.icon_wrap{
    position: absolute;
    left: 26%;
    top: 240px;
    font-size: 22px;
    color: #fff;
    display: flex;
    gap: 16px;
    align-items: center;
    cursor: pointer;
}
.modal_img button{
    position: absolute;
    left: 20px;
    top: 236px;
    border: none;
    padding: 6px 22px;
    background: #fff;
    border-radius: 4px;
    font-weight: bold;
}
.modal_img button:hover{
    color: red;
}
.modal_list h4{
    color: #fff;
    font-weight: bold;
    font-size: 24px;
}
.modal_list{
    color: #fff;
    padding: 20px;
}
.modal_list h5{
    margin-top: 10px;
}
.modal_list h6{
    margin: 6px 0 10px 0;
}
.modal_list p{
    font-size: 14px;
    color: #ccc;
    line-height: 22px;
}