.footer{
    width: 100%;
    background: #032541;
}
.footer .inner{
    display: flex;
    justify-content: center;
    margin: 0 auto;
    width: 1000px;
    height: 320px;
    align-items: center;
    box-sizing: border-box;
}
.footer img{
    width: 120px;
    margin-bottom: 20px;
}
.footer_wrap{
    margin-right: 40px;
}
.footer .cumu{
    padding: 6px 12px;
    border-radius: 4px;
    background: #fff;
    color: #12b6dc;
    font-weight: bold;
    text-align: center;
    cursor: pointer;
}
.footer .cumu:hover{
    background: #12b6dc;
    color: #fff;
}
.list_wrap{
    display: flex;
    gap: 48px;
}
.list_wrap ul li{
    cursor: pointer;
    color: #fff;
    font-size: 14px;
}
.list_wrap ul li:hover{
    color: #12b6dc;
    text-decoration: underline;
}
.list_wrap ul .list{
    font-size: 16px;
    font-weight: bold;
}