.page_wrap{
    width: 1100px;
    margin: 40px auto;
    color: #000;
}
h1{
    margin-bottom: 20px;
}
.page p{
    font-family: 100;
}
.page .p2{
    margin: 12px 0 40px 0;
}
.page p span{
    color: skyblue;
    cursor: pointer;
}
.page p span:hover{
    text-decoration: underline;
}
button{
    padding: 4px 8px;
    cursor: pointer;
}
button:hover{
    background: #000;
    color: #fff;
}
.page_wrap .inputbox{
    width: 100%;
}
.page_wrap .inputbox input{
    width: 100%;
    padding: 10px 0 ;
    margin: 4px 0 20px 0;
    border: 1px solid #ccc;
    border-radius: 4px;
    text-indent: 10px;
}
.page_wrap .loginbox {
    display: flex;
    gap: 8px;
    align-items: center;
}
.page_wrap .loginbox button{
    background: #02b4e4;
    color: #fff;
    font-weight: bold;
    padding: 6px 12px;
    border: none;
    border-radius: 6px;
}
.page_wrap .loginbox button:hover{
    background: #ccc;
    color: #fff;

}
.page_wrap .loginbox p{
    color: #02b4e4;
    font-size: 14px;
}