.movie{
    width: 1200px;
    margin: 0 auto;
}
.poster{
    display: flex;
    width: 1200px;
    margin-top: 20px;
}
.tit {
    display: flex;
    gap: 20px;
    align-items: center;
    padding: 40px 0 20px 0;
}
.tit ul{
    display: flex;
    gap: 30px;
    align-items: center;
    border: 2px solid #33d9ab;
    border-radius: 30px;
    padding: 0 10px 0 0;
    background: rgba(255,255, 255, 0.4);
}
.tit ul li:first-child{
    height: 100%;
    padding: 0 10px;
    border: 2px solid #33d9ab;
    border-radius: 30px;
    background: #33d9ab;
    
}

.poster img{
    width: 100%;
    border-radius: 8px;
    object-fit: contain;
    max-width:  200px;
    margin-right: 10px;
    transition: all 0.5s ease-in-out;
    cursor: pointer;
}
.poster img:hover{
    transform: scale(1.1);
}
.poster .info{
    margin-top: 8px;
}
.poster .info p{
    font-weight: bold;
}
.poster .info span{
    font-size: 12px;
    color: #888;
    font-weight: bold;
}