.trend_wrap{
    width: 1300px;
    height: 350px;
    margin: 0 auto;
    box-sizing: border-box;
    padding: 80px 40px;
    background: #00497a;
    color: #fff;
}
.trend_wrap p:first-child{
    font-size: 40px;
    font-weight: bold;
}
.trend_wrap p{
    font-size: 26px;
    font-weight: bold;
}
.trend_wrap label input{
    border: none;
    background: #fff;
    border-radius: 12px;
    margin-top: 40px;
    width: 88%;
    padding: 12px;
}
.trend_wrap .serch{
    position: relative;
}
.trend_wrap .serch input[type=submit]{
    width: 100px;
    color: #fff;
    font-weight: bold;
    border: none;
    background: #15cbbc;
    border-radius: 12px;
    padding: 12px;
    position: absolute;
    right: 10%;
    top: 40px;
    cursor: pointer;
}
.trend_wrap .serch input[type=submit]:hover{
    color: #000;
}

